////
/// @group grid
/// @author [*/dxg](https://dxg.wolterskluwer.io/)
/// @since 1.20.11
/// @access public
////

/// Breakpoint slices
///
/// @type Map
$wk-grid-breakpoint-slices: (
    xs: 0,
    s: wk-rem(600px),
    m: wk-rem(1024px),
    l: wk-rem(1440px),
    xl: wk-rem(1600px),
    xxl: wk-rem(1920px)
) !default;

/// Breakpoints values
///
/// @type List
$wk-grid-slicer-breakpoints: map-values($wk-grid-breakpoint-slices);

/// Names of the breakpoint slicers
///
/// @type List
$wk-grid-slicer-breakpoint-names: map-keys($wk-grid-breakpoint-slices);

$wk-grid-slicer-anti-overlap-corrections: -1px, -0.0625rem;

/// Total amount of columns
///
/// @type Number
$wk-grid-global-columns: 12 !default;

/// Grid gap between columns
///
/// @type Unit
$wk-grid-gap: $wk-grid-padding * 2 !default;

/// Grid column width for column base for mobile
///
/// @type Unit
$wk-grid-column-base-flex-basis-xs: calc(
    25% - #{$wk-grid-gap} * 3 / 4
) !default;

/// Grid column width for column base for tablet
///
/// @type Unit
$wk-grid-column-base-flex-basis-s: calc(
    12.5% - #{$wk-grid-gap} * 7 / 8
) !default;

$wk-grid-column-base: calc(1 / 12 * 100%);

/// Grid column width for column base
///
/// @type Unit
$wk-grid-column-base-flex-basis-default: calc(
    #{$wk-grid-column-base} - #{$wk-grid-gap} * 11 / 12
) !default;

/// Grid column width map for main breakpoints
///
/// @type Map
$wk-grid-column-width: (
    xs: $wk-grid-column-base-flex-basis-xs,
    s: $wk-grid-column-base-flex-basis-s,
    m: $wk-grid-column-base-flex-basis-default
) !default;

$wk-grid-column-width-workflow: (
    xs: $wk-grid-column-base-flex-basis-xs,
    s: $wk-grid-column-base-flex-basis-s,
    m: $wk-grid-column-base-flex-basis-default,
    l: $wk-grid-column-base-flex-basis-default,
    xl: $wk-grid-column-base-flex-basis-default,
    xxl: $wk-grid-column-base-flex-basis-default
) !default;

/// Page Container

/// Page Container paddings for research based products
///
/// @type Map
$wk-grid-page-container-paddings: (
    xs: wk-rem(16px),
    s: wk-rem(32px),
    m: wk-rem(48px),
    l: wk-rem(56px),
    xl: wk-rem(80px),
    xxl: wk-rem(168px)
) !default;

/// Page Container paddings for workflow based products
///
/// @type Map
$wk-grid-page-container-paddings-workflow: (
    xs: wk-rem(16px),
    s: wk-rem(16px),
    m: wk-rem(32px)
) !default;

/// Page Container paddings for workflow based products
///
/// @type Map
$wk-grid-stage-container-paddings-workflow: (
    xs: wk-rem(16px),
    s: wk-rem(16px),
    m: wk-rem(32px),
    l: wk-rem(32px),
    xl: wk-rem(32px),
    xxl: wk-rem(32px)
) !default;

/// Page Container desktop max-width
///
/// @type Unit
$wk-grid-page-container-max-width-desktop: $wk-global-max-width !default;

/// Main grid container class name
///
/// @type String
$wk-grid-column-group-class-name: 'wk-grid' !default;

/// Main grid column class name
///
/// @type String
$wk-grid-column-class-name: 'wk-column' !default;

/// Main "margin" page container class name for research
///
/// @type String
$wk-grid-page-container-class-name: 'wk-grid-page-container' !default;

/// Main "margin" page container class name for workflow
///
/// @type String
$wk-grid-page-workflow-container-class-name: 'wk-grid-page-workflow-container' !default;

/// Main "margin" stage container class name for workflow
///
/// @type String
$wk-grid-stage-workflow-container-class-name: 'wk-grid-stage-workflow-container' !default;
