@import '~@wk/fundamentals/helpers.utils';
@import 'grid.variables';
@import 'grid.mixins';
@import '../breakpoint/breakpoint.mixins';
@import '../breakpoint/breakpoint.functions';

.#{$wk-grid-page-container-class-name} {
    @include grid-page-container;
}

.#{$wk-grid-page-workflow-container-class-name} {
    @include grid-page-container($workflow: true);
}

.#{$wk-grid-page-container-class-name},
.#{$wk-grid-page-workflow-container-class-name} {
    @include bp-based-pure-grid;
}

.#{$wk-grid-stage-workflow-container-class-name} {
    @include grid-stage-container-workflow;

    // generate class `.wk-column-{1-12}`
    $i: 1;
    @while $i <= $wk-grid-global-columns {
        .#{$wk-grid-column-class-name}-#{$i} {
            @include grid-column($n: $i, $from: null);
        }
        $i: $i + 1;
    }

    @each $slice-name in $wk-grid-slicer-breakpoint-names {
        $col-media: '';

        &.wk-stage-#{$slice-name} {
            // generate class `.wk-column`
            .#{$wk-grid-column-class-name},
            [class*='#{$wk-grid-column-class-name}'] {
                $width: map-get($wk-grid-column-width-workflow, $slice-name);
                flex-basis: $width;
                max-width: $width;
            }

            .wk-is-hidden-#{$slice-name} {
                display: none !important;
            }

            $col-media: '-#{$slice-name}';

            $i: 1;

            // generate class `.wk-column-{1-12}-{xs-xxl}`
            @while $i <= $wk-grid-global-columns {
                .#{$wk-grid-column-class-name}-#{$i}#{$col-media} {
                    @include grid-column($n: $i, $from: null);
                }
                $i: $i + 1;
            }
        }
    }
}

.#{$wk-grid-column-group-class-name} {
    @include grid-column-group;
}
