@import 'grid.variables';
@import '../breakpoint/breakpoint.functions';
@import '../breakpoint/breakpoint.mixins';

// Grid mixins

/// Container for columns. Inside it the columns will be aligned in a row.
///
/// @require {Variable} $wk-grid-gap
///
/// @example scss - Usage
///   .row {
///       @include grid-column-group;
///       .col-12 {
///           @include grid-column(12)
///       }
///   }
///
@mixin grid-column-group {
    display: flex;
    flex-wrap: wrap;
    gap: $wk-grid-gap;
}

/// In case there are few columns in a row their width will be equal to each other.
/// @require {Mixin} grid-from
/// @require {Variable} $$wk-grid-column-width
///
/// @example scss - Usage
/// .col {
///     @include grid-column-base;
/// }
///

@mixin grid-column-base {
    flex-basis: 0;

    @each $slice, $width in $wk-grid-column-width {
        @include grid-from($slice) {
            flex-basis: $width;
            max-width: $width;
        }
    }
}

// Utils

/// Creates column with $n width and $from breakpoint
///
/// @require {Mixin} grid-at
/// @require {Variable} $$wk-grid-column-base-flex-basis-default
/// @require {Variable} $wk-grid-gap
///
/// @param {Number} $n [12] - width value
/// @param {String} $from [mobile] - breakpoint name
///
/// @example - Usage
///  .col-12 {
///     @include grid-column(12);
///}
///
@mixin grid-column($n: 12, $from: xs) {
    $width: calc(
        #{$n} * (#{$wk-grid-column-base-flex-basis-default} + #{$wk-grid-gap}) -
            #{$wk-grid-gap}
    );
    @if $from != null {
        @include grid-at($from) {
            flex-basis: $width;
            max-width: $width;
        }
    } @else {
        flex-basis: $width;
        max-width: $width;
    }
}

/// Creates a container. There are two usage of container
/// - for content;
/// - for grid (columns);
///
/// @require {Mixin} $grid-from
/// @require {Variable} $wk-grid-page-container-paddings
/// @require {Variable} $wk-grid-page-container-paddings-workflow
///
/// @param {Boolean} $workflow [false] - workflow pattern indicator
///
/// @example - Usage
/// .container {
///     @include grid-page-container();
///     .col {
///         @include grid-column(12);
///     }
/// }
///
@mixin grid-page-container($workflow: false) {
    margin-left: auto;
    margin-right: auto;
    width: 100%;

    $paddings-map: $wk-grid-page-container-paddings;

    @if $workflow {
        $paddings-map: $wk-grid-page-container-paddings-workflow;
    }

    @each $breakpoint, $padding in $paddings-map {
        @include grid-from($breakpoint) {
            padding-left: $padding;
            padding-right: $padding;
        }
    }
}

@mixin grid-stage-container-workflow {
    margin-left: auto;
    margin-right: auto;
    width: 100%;

    $paddings-map: $wk-grid-stage-container-paddings-workflow;

    @each $breakpoint, $padding in $paddings-map {
        &.wk-stage-#{$breakpoint} {
            padding-left: $padding;
            padding-right: $padding;
        }
    }
}

@mixin bp-based-pure-grid {
    .#{$wk-grid-column-class-name},
    [class*='#{$wk-grid-column-class-name}'] {
        @include grid-column-base;
    }

    // generate class `.wk-column-{1-12}`
    $i: 1;
    @while $i <= $wk-grid-global-columns {
        .#{$wk-grid-column-class-name}-#{$i} {
            @include grid-column($n: $i, $from: null);
        }
        $i: $i + 1;
    }

    // generate class `.wk-column-{1-12}-{xs-xxl}`
    @each $slice-name in $wk-grid-slicer-breakpoint-names {
        $col-media: '';

        .wk-is-hidden-#{$slice-name} {
            @include grid-at($slice-name) {
                display: none !important;
            }
        }

        $col-media: '-#{$slice-name}';

        $i: 1;

        @while $i <= $wk-grid-global-columns {
            .#{$wk-grid-column-class-name}-#{$i}#{$col-media} {
                @include grid-column($n: $i, $from: $slice-name);
            }
            $i: $i + 1;
        }
    }
}
