@import '~bootstrap/dist/css/bootstrap.css';
@import '~@wk/components-react16/dist/all.min.css';
@import '~@wk/grid/grid.scss';
@import './common/scss/media-queries';

html {
  font-size: 14px;
}

body {
  font-family: fira sans, helvetica neue, Helvetica, Roboto, Arial, sans-serif;
  font-weight: 400;
  background: #f6f6f6;
  color: #232323;
  overflow-x: hidden;
}

nav ol,
nav ul,
ol,
ul {
  list-style: none;
  list-style-image: none;
  margin: 0;
  padding: 0;
}

img {
  -ms-interpolation-mode: bicubic;
  border: 0;
  height: auto;
  max-width: 100%;
  vertical-align: middle;
}

a {
  color: #007ac3;
  text-decoration: none;
  background-color: transparent;
}

h2 {
  font-size: 1.4rem;
  margin-top: 0.5rem;
}

h3 {
  font-size: 1.2rem;
  color: #ff7f50;
}

.cg-vertical-nav-icon-label-2-5-1 {
  flex-direction: column;
  align-items: center;
}

.cg-vertical-nav-icon svg {
  height: 32px !important;
  width: 32px !important;
}

.cg-vertical-nav-icon-label-2-5-1.cg-is-sticky {
  padding: 0.5rem 0;
}

.cg-vertical-nav-icon-label-2-5-1 .cg-vertical-nav-icon {
  margin-right: 0;
}

.profile-uc {
  &__list {
    .profile-uc--card-view & {
      @include mq-redesign(s) {
        display: grid;
        gap: 1.6rem;
        margin: 0 0-3.2rem;
      }

      @include mq-redesign(s) {
        grid-template-columns: repeat(auto-fill, minmax(18.4rem, 1fr));
      }
    }
  }

  &__list-item {
    border-bottom: 1px solid #dadada;
    margin: 0 0 2.4rem;
    padding: 0 0 2.4rem;
  }
}

.profile-image {
  text-align: center;
  aspect-ratio: 1 / 1;
  width: 100%;
}

.profile-search {
  padding-bottom: 2rem;
  margin-bottom: 2rem;
  border-bottom: solid 1px #dadada;
}

.profile-container {
  width: 90%;
  margin: 0 auto;
  -webkit-box-shadow: 0px 0px 20px 0px rgba(179, 179, 179, 1);
  -moz-box-shadow: 0px 0px 20px 0px rgba(179, 179, 179, 1);
  box-shadow: 0px 0px 20px 0px rgba(179, 179, 179, 1);
}

.profile-home {
  padding: 2rem;
  min-height: calc(100vh - 89px);
}

.profile-user-detail {
  position: relative;
  border: 1px solid #e5e5e5;
  background: #fff;
  padding: 2rem 1.5rem 0;
  margin-bottom: 1.5rem;
  display: block;
  min-height: 195px;
  height: 100%;
  word-wrap: break-word;
  cursor: pointer;
}

.user-profile-pic {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  margin-bottom: 1.5rem;
}

.profile-info {
  margin: 2rem;
}

.user-applications {
  margin-bottom: 1rem;
}

.profile-pill-EJP {
  margin: 1rem 0.5rem 1rem 0;
  .cg-pill-ctx-2-5-1 .cg-pill.cg-pill-suggestion {
    background-color: #e9edc9;
    margin-bottom: 0.5rem;
  }
}

.profile-pill-LDI {
  margin: 1rem 0.5rem 1rem 0;
  .cg-pill-ctx-2-5-1 .cg-pill.cg-pill-suggestion {
    background-color: #ebd2f3;
    margin-bottom: 0.5rem;
  }
}

.profile-pill-OnDemand,
.profile-pill-ondemand {
  margin: 1rem 0.5rem 1rem 0;
  .cg-pill-ctx-2-5-1 .cg-pill.cg-pill-suggestion {
    background-color: #ffe5ec;
    margin-bottom: 0.5rem;
  }
}

.profile-pill-LWW-Shop {
  margin: 1rem 0.5rem 1rem 0;
  .cg-pill-ctx-2-5-1 .cg-pill.cg-pill-suggestion {
    background-color: #f5deb3;
    margin-bottom: 0.5rem;
  }
}

.profile-pill-LCP {
  margin: 1rem 0.5rem 1rem 0;
  .cg-pill-ctx-2-5-1 .cg-pill.cg-pill-suggestion {
    background-color: #f5c7f7;
    margin-bottom: 0.5rem;
  }
}

.address-pill-Delivery {
  .cg-pill-ctx-2-5-1 .cg-pill.cg-pill-suggestion {
    background-color: #9FE2BF;
    margin-bottom: 0.5rem;
  }
}

.address-pill-Business {
  .cg-pill-ctx-2-5-1 .cg-pill.cg-pill-suggestion {
    background-color: #CCCCFF;
    margin-bottom: 0.5rem;
  }
}

.user-profile-container {
  display: flex;
}

.error-view-container,
.loading-container {
  min-height: calc(100vh - 89px);
  display: flex;
  justify-content: center;
  align-items: center;
}

.profile-menu,
.profile-info {
  width: 100px;
}

.profile-menu {
  min-height: calc(100vh - 89px);
  background-color: #fafafa;
  padding-top: 1rem;
  overflow: auto;
}

.profile-info {
  flex-grow: 1;
}

.user-profile-details {
  background: #fff;
  padding: 1rem;
  border: 1px solid #e5e5e5;
}

.user-data {
  display: inline-flex;
  flex-direction: column;
  vertical-align: bottom;
  margin-left: 1.5rem;
}

.user-name {
  margin-bottom: 0.5rem;
  text-transform: capitalize;
}

.back-to-profile {
  text-align: right;
  cursor: pointer;
  color: #007ac3;
}

.address-name {
  font-weight: bold;
}

.cg-card-container-2-5-1 {
  border-radius: 10px;
}

.cg-product-selector-2-5-1 {
  padding: 0 1rem;
}

.cg-product-switcher-2-5-1 .cg-product-switcher-selector-heading {
  display: none;
}

.text-right {
  text-align: right;
}

.smokebreak-pds-suggestion-header {
  width: 100%;
}

.edit-button-container {
  text-align: right;
}
.button-position {
  margin-top: -3rem;
}

.site-notification {
  position: fixed;
  top: 1rem;
  right: 10%;
  width: 25rem;
}
