@import 'base.variables';

/// Converts one or more list values to a specified type.
///
/// @author [Pierre Burel](https://github.com/pierreburel/sass-rem) - Modified by */dxg
///
/// @param {String} $to - conversion type
/// @param {List} $values... - list of values to convert
///
/// @example scss - Usage
///   div {
///       text-shadow: wk-rem-convert(rem, 1px 1px #eee, -1px -1px #eee);
///   }
///
@function wk-rem-convert($to, $values...) {
    $result: ();
    $separator: list-separator($values);

    @each $value in $values {
        @if type-of($value) ==
            'number' and
            unit($value) ==
            'rem' and
            $to ==
            'px'
        {
            $result: append($result, $value / 1rem * $wk-rem-base, $separator);
        } @else if
            type-of($value) ==
            'number' and
            unit($value) ==
            'px' and
            $to ==
            'rem'
        {
            $result: append($result, $value / $wk-rem-base * 1rem, $separator);
        } @else if type-of($value) == 'list' {
            $result: append(
                $result,
                wk-rem-convert($to, $value...),
                $separator
            );
        } @else {
            $result: append($result, $value, $separator);
        }
    }

    @if (length($result) == 1) {
        @return nth($result, 1);
    } @else {
        @return $result;
    }
}

/// Convert list of values to `rem`.
///
/// If `$wk-rem-px-only` is set to `true`, returned values will be `px`
///
/// @author [Pierre Burel](https://github.com/pierreburel/sass-rem) - Modified by */dxg
///
/// @param {List} $values... - list of values to convert
///
/// @example scss - Usage
///   div {
///       text-shadow: 1px 1px #eee, -1px -1px #eee;
///   }
///
/// @return {Number | List}
///
/// @require $wk-rem-px-only
/// @require wk-rem-convert
@function wk-rem($values...) {
    @if $wk-rem-px-only {
        @return wk-rem-convert(px, $values...);
    } @else {
        @return wk-rem-convert(rem, $values...);
    }
}

@function wk-less-than($n) {
    $selector-base: '& > *:nth-last-child(-n+#{$n - 1}):first-child';

    @return '#{$selector-base}', '#{$selector-base} ~ *';
}

// TODO: add documentation
@function wk-modulo($mod, $keep-first: true) {
    $selector: join((), (), comma);

    @for $i from 2 through $mod {
        $selector: append(
            $selector,
            '& > *:nth-child(#{$mod}n):nth-last-child(#{$i}) ~ *'
        );
    }
    @if not $keep-first {
        $selector: append($selector, wk-less-than($mod));
    }

    @return $selector;
}

/// Calculate unitless line height
///
/// @param {String} $line-height - line height
/// @param {String} $font-size - font size
///
/// @example scss - Usage
///   div {
///       line-height: wk-line-height(20px, 1rem);
///   }
///
/// @require wk-rem

@function wk-line-height($line-height, $font-size) {
    @if unit($line-height) != unit($font-size) {
        @if unit($line-height) != 'rem' {
            $line-height: wk-rem($line-height);
        }

        @if unit($font-size) != 'rem' {
            $font-size: wk-rem($font-size);
        }
    }

    @return round($line-height / $font-size * 100) * 0.01; // cut infinite fraction
}
