////
/// @group grid
/// @author [*/dxg](https://gpo.wolterskluwer.com/)
/// @since 1.20.11
/// @access public
////

@import 'breakpoint.functions';

@mixin grid-between($slice-left, $slice-right) {
    // Parsing slice names.
    $slice-left: grid-get-slice-id($slice-left);
    $slice-right: grid-get-slice-id($slice-right);

    // If the slices provided are the first and the last one,
    // the breakpoint becomes meaningless
    @if (($slice-left == 1) and ($slice-right == grid-total-slices())) {
        //Thus, don't wrap the code block with a media query
        @content;
    } @else {
        // Retrieving the left edge of the left slice
        $context: grid-left-bp-of-slice($slice-left);

        // Retrieving the right edge of the slice
        // unless the slice is the last one
        @if ($slice-right < grid-total-slices()) {
            $right: grid-right-bp-of-slice($slice-right);
            $context: append($context, $right);
        }

        // Setting the breakpoint
        @include grid-media-query($context) {
            @content;
        }
    }
}

@mixin grid-media-query($query-string) {
    // breakpoint (https://github.com/at-import/breakpoint) has a interface for arguments that could be expressed like
    // (680px) => meaning (min-width: 680px)
    // (max-width 1200px) => meaning (max-width: 1200px)
    // (680px 1200px) => meaning (min-width: 680px) and (max-width: 1200px)

    $media: '';

    @if (length($query-string) == 1) {
        $media: '(min-width: #{$query-string})';
    } @else {
        $first: nth($query-string, 1);
        $second: nth($query-string, 2);

        @if ($first == max-width) {
            $media: '(max-width: #{$second})';
        } @else {
            $media: '(min-width: #{$first}) and (max-width: #{$second})';
        }
    }

    @media #{$media} {
        @content;
    }
}

// Wraps the content block provided with a media query
// with min-width equal to the right edge of a slice
//
// from($slice, $no-query: false)
// - $slice : <slice number> A number of a slice. Should be a positive integer.
@mixin grid-from($slice) {
    @include grid-between($slice, grid-total-slices()) {
        @content;
    }
}

// Wraps the content block provided with a media query
// with min-width and max-width equal to the edges of a slice
//
// at($slice, $no-query: false)
// - $slice : <slice number> A number of a slice. Should be a positive integer.
@mixin grid-at($slice) {
    @include grid-between($slice, $slice) {
        @content;
    }
}

// Wraps the content block provided with a media query
// with max-width equal to the right edge of a slice
//
// to($slice, $no-query: false)
// - $slice : <slice number> A number of a slice. Should be a positive integer.
@mixin grid-to($slice) {
    @include grid-between(1, $slice) {
        @content;
    }
}
